.profile-section {
    
}

.profile-name {
    padding-top: 7%;
    font-size: 3.5em;
    color: white;  /* Using the complementary color for the name for better contrast */
    margin-bottom: 0%; /* Adding a little margin-bottom for breathing room */
    font-family: 'Almarai', sans-serif;
    line-height: 1.2; /* Adjusting line-height for better vertical rhythm */
}

.profile-occupation {
    font-size: 1.3em;
    color: white; /* Using the darker shade of green */
    margin: 0.5em 0; /* Adding top and bottom margins for better spacing */
    font-family: 'Almarai', sans-serif;
    line-height: 1.3;
}

.profile-description {
  font-size: 1.3em;
  color: #5F9A80; /* Using the primary color for consistency */
  margin: 1em 0; /* Same margin as the occupation for consistency */
  font-family: 'Almarai', sans-serif;
  max-width: 70%; /* Or any percentage that works best for your layout */
  font-weight: 570;
}



#icons {
    position: relative;
    padding-top: 5%;
    left: 49%;
    transform: translate(-50%, -50%);
  }
  
  .icon-link {
    padding: 3px;
    display: inline-block;
  }
  
  .icon-link:hover {
    transform: scale(1.05);
  }
  
  #tooltip {
    position: relative;
    display: inline-block;
  }
  
  .tooltiptext {
    font-family: 'Almarai', sans-serif;
    visibility: hidden;
    background-color: black;
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 5px 10px;
    white-space: nowrap;
    /* Position the tooltip */
    position: absolute;
    z-index: 9999;
    bottom: -80%;
    left: 50%;
    transform: translateX(-50%);
    /* Add margin */
    margin-bottom: 10px;
  }
  
  
  #tooltip:hover .tooltiptext {
    visibility: visible;
  }

@media (max-width: 800px) {
    .profile-name {
        padding-top: 0%;
    }
}